import axios from "axios";

const service=axios.create({
    baseURL: 'http://rap2api.taobao.org/app/mock/278481',//会自动加载接口地址上
    timeout: 1000,//指定请求超时的毫秒数,请求花费的时间>timeout,请求会被中断
    headers: {'X-Custom-Header': 'application/json; charset=utf-8'}
})


// 添加请求拦截器
service.interceptors.request.use(function (config) {
    // 在发送请求之前做些什么

//   var username = localStorage.getItem('username');
//   var formName = localStorage.getItem('formName');
if (localStorage.getItem('Authorization')) {
    config.headers.Authorization = localStorage.getItem('Authorization');
  }

  return config;
  }, function (error) {
    // 对请求错误做些什么
    return Promise.reject(error);
  });

// 添加响应拦截器
service.interceptors.response.use(function (response) {
    // 对响应数据做点什么
    return response;
  }, function (error) {
    // 对响应错误做点什么
    return Promise.reject(error);
  });

  export default service


