<template>
    <div class="class-list">
        <template>
            <el-form :inline="true" :model="formInline" align="left" class="demo-form-inline">
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">新增</el-button>
                </el-form-item>
                <el-form-item label="姓名">
                    <el-input v-model="formInline.name" placeholder="姓名"></el-input>
                </el-form-item>
                <el-form-item label="学号">
                    <el-input v-model="formInline.user" placeholder="学号"></el-input>
                </el-form-item>
                <el-form-item label="班级">
                    <el-select v-model="formInline.region" placeholder="班级">
                    <el-option label="框架9班" value="9"></el-option>
                    <el-option label="框架10班" value="10"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="oo">查询</el-button>
                </el-form-item>
            </el-form>
            <el-table :data="tableData" border style="width: 100%">
                <el-table-column prop="name" align="center" label="姓名" width="100"></el-table-column>
                <el-table-column prop="number" align="center" label="学号" width="100"></el-table-column>
                <el-table-column prop="age" align="center" label="年龄"></el-table-column>
                <el-table-column prop="sex" align="center" label="性别"></el-table-column>
                <el-table-column prop="class" align="center" label="班级"></el-table-column>
                <el-table-column prop="address"  align="center" label="地址"></el-table-column>
                <el-table-column prop="phone" align="center" label="联系方式"></el-table-column>
                <el-table-column align="center" label="操作">
                    <template slot-scope="scope">
                        <el-button type="primary" size="mini" icon="el-icon-edit" @click="updateInfo(scope.row)" ></el-button>
                        <el-button type="danger" size="mini" icon="el-icon-delete" @click="update()"></el-button>
                    </template>
                </el-table-column>
            </el-table>
                <el-dialog :title=" state ? '修改学生信息':'新增学生信息'"  :visible.sync="dialogFormVisible">
                <el-form :model="form">
                    <el-form-item label="姓名" :label-width="formLabelWidth">
                    <el-input v-model="form.name" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="学号" :label-width="formLabelWidth">
                    <el-input v-model="form.number" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="年龄" :label-width="formLabelWidth">
                    <el-input v-model="form.age" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="性别" :label-width="formLabelWidth">
                        <el-radio v-model="form.sex" label="1">男</el-radio>
                        <el-radio v-model="form.sex" label="2">女</el-radio>
                    </el-form-item>
                    <el-form-item label="班级" :label-width="formLabelWidth">
                    <el-input v-model="form.class" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="地址" :label-width="formLabelWidth">
                    <el-input v-model="form.address" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="联系方式" :label-width="formLabelWidth">
                    <el-input v-model="form.phone" autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
                </div>
                </el-dialog>
            <Pageing></Pageing>
        </template>
    </div>
</template>
<script>
import Pageing from "../common/Pageing"
export default {
    components:{
    Pageing
  },
    data (){
        return {
            dialogFormVisible: false,
        form: {},
        state:true,
        formLabelWidth: '120px',
          formInline: {
          user: '',
          region: ''
        },
        tableData: []
        }
    },
    created(){
        this.getData()
    },
    methods:{
        updateInfo(row){
            this.form={...row}
            this.dialogFormVisible = true
            this.state=true
        //    this.formInline =row;
           console.log(row);
        //    console.log(this.formInline);
        },
        getData(){
            var that=this
            this.service.get('/studentlist').then(
            function(response){
                if(response.data.status==="200"){
                   that.tableData=[...response.data.data]
                   that.tableData.forEach((item)=>{
                      item.sex=item.sex==="1"?'男':'女'
                   })
                }
            }
        )
        },
        onSubmit() {
            this.dialogFormVisible = true
            this.form={sex: "1"}
            this.dialogFormVisible = true
            this.state=false
      }
    }
}
</script>
<style lang="scss">
</style>


