<template>
    <div class="data-over-viwe">
        <div id="mian"></div>
    </div>
</template>
<script>
import echarts from 'echarts';
export default {
    name:'DataOverView',
    data(){
        return {

        }
    },
    mounted(){
        var myChart = echarts.init(document.getElementById('main'));

        // 指定图表的配置项和数据
        myChart.setOption({
            title: {
                text: 'ECharts 入门示例'
            },
            tooltip: {},
            xAxis: {
                data: ['衬衫', '羊毛衫', '雪纺衫', '裤子', '高跟鞋', '袜子']
            },
            yAxis: {},
            series: [{
                name: '销量',
                type: 'bar',
                data: [5, 20, 36, 10, 10, 20]
            }]
        });
    }
}

</script>
<style lang="scss">
    .data-over-viwe{
        #mian{
            width: 500px;
            height: 300px;
        }
    }
</style>


