import Vue from 'vue'
import Router from 'vue-router'
// import Home from '@/components/Home'
// import Login from '@/components/Login'
// import NotFound from '@/components/NotFound'
// import StudentList from '@/components/students/StudentList'
// import WorkList from '@/components/students/WorkList'
// import DataOverView from '@/components/dataAnalyss/DataOverView'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/login',
      hidden:true
    },
    {
      path: '/home',
      name: '学生管理',
      components: require('@/components/Home.vue'),
      iconClass:'fa fa-th-list',
      children:[
           {
             path :'/home/studentlist',
             name:'学生列表',
             components:require('@/components/students/StudentList.vue'),
             iconClass:'fa fa-graduation-cap',
           },
           {
            path :'/home/worklist',
            name:'作业列表',
            components:require('@/components/students/WorkList.vue'),
            iconClass:'fa fa-user-circle',
           },
           {
            path: '/DataOverView',
            name: '数据视图',
            components: require('@/components/dataAnalyss/DataOverView.vue'),
            iconClass:'fa fa-table',
          },
        ]
    },
    {
      path: '/login',
      name: 'Login',
      components: require('@/components/Login.vue'),
      hidden:true
    },
    
    {
      path: '*',
      name: 'NotFound',
      components: require('@/components/NotFound.vue'),
      hidden:true
    }
  ]
});

