<template>
<div class="login">
    <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span class="title">后台管理系统</span>
        </div>
        <el-form ref="form" :model="form" :rules="rules" label-width="80px" class="form-box">
            <el-form-item label="用户名" prop="username">
              <el-input v-model="form.username"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input v-model="form.password"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="submitForm('form')" class="loginBtn">立即登录</el-button>
            </el-form-item>
          </el-form>
      </el-card>
</div>
</template>
<script>
export default {
  name: 'Login',
  data () {
    return {
        form: {
          username: '',
          password:'',
          region: '',
        },
        rules:{
         username: [
            { required: true, message: '请输入活动名称', trigger: 'blur' },
            { min: 3, max: 12, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请输入活动名称', trigger: 'blur' },
            { min: 5, max: 10, message: '长度在 5 到 10 个字符', trigger: 'blur' }
          ],
        }
      }
    },
    methods: {
      submitForm(formName) {
        var that=this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.service.post('/login',that.formName)
                .then(function (response) {
                  console.log(that.formName);
                  if(response.data.status==='200'){
                    setTimeout(function(){
                      console.log(response);
                      that.$router.push('/home')
                    },1000)
                    that.$message({
                        message: response.data.msg,
                        type: 'success',
                        duration:1000
                      });
                      that.userToken =response.data.data.token;
                      that.$store.commit('changeLogin', { Authorization: that.userToken })
                      // localStorage.setItem('formName',this.formName)
                  }
                })
                .catch(function (error) {
                  console.log(error);
                });
            // this.$router.push('home')
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
  }
}
</script>
<style lang='scss'>
.login{
  width: 100vw;
  height: 100vh;
  background:#409EFF;
  .box-card{
    width: 480px;
    position: absolute;
    top:200px;
    left: 50%;
    transform: translateX(-50%);
    .title{
      font-size: 28px;
    }
  }
  .form-box{
    width: 350px;
  }
  .loginBtn{
    width: 100%;
  }
}
</style>


