<template>
  <div class="home">
    <el-container>
        <el-header>
          <div class="header-left">
            <span v-show="!isCollapse">
              春雨后台
            </span>
            <i :class="isCollapse?'coll el-icon-s-unfold':'coll el-icon-s-fold'" @click="isCollapse=!isCollapse"></i>
          </div>
          <div class="header-right" @click="lignOut">
          <span class="username"><i class="el-icon-user"></i>{{username}}</span>
           <span class="username">退出</span>
          </div>
        </el-header>
        <el-container class="main">
          <el-aside :width="!isCollapse?'200px':'65px'">
            <el-menu router :default-active="activePath" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse">
              <template v-for="(item, index) in meuns">  
                    <el-submenu :index="item.path" :key='index' v-if="!item.hidden">
                      <template slot="title">
                        <i :class="item.iconClass"></i>
                        <span slot="title">{{item.name}}</span>
                      </template>
                      <el-menu-item-group v-for="(child, index) in item.children" :key="index">
                        <el-menu-item :index="child.path"><i :class="child.iconClass">{{child.name}}</i></el-menu-item>
                      </el-menu-item-group>
                    </el-submenu>
              </template>
            </el-menu>
          </el-aside>
          <el-main>
            <el-card>
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item v-for="(item,index) in $route.matched" :key="index">{{item.name}}</el-breadcrumb-item>
              </el-breadcrumb>
            </el-card>
            <router-view></router-view>
          </el-main>
        </el-container>
      </el-container>
  </div>
</template>

<script>
// import Menu from "./common/Menu"
export default {
  name: 'Home',
  data () {
        return {
        isCollapse: true,
        meuns:[],
        activePath:'',
        route:[],
        username:''
      };
      
  },

  created(){
    this.meuns=[...this.$router.options.routes];
    this.activePath=this.meuns[1].children[1].path;
    console.log(this.meuns);
    this.username=localStorage.username;
  },
  methods:{
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      lignOut() {
        var that=this;
        that.$alert('确定退出吗？', '退出登录', {
          confirmButtonText: '确定',
          callback: action => {
            that.$router.push('/login'),
            localStorage.clear();
            this.$message({
              type: 'info',
              message: `退出成功`,
            });
          }
        });
      }
  }

  // methods: {
  //
  //   }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
.home{
  .el-header{
      background-color: #409EFF;
      color: #fff;
      text-align: center;
      line-height: 60px;
      display: flex;
      justify-content: space-between;
      .header-left{
        .coll{
          cursor: pointer;
        }
      }
      .header-right{
       .username{
        display: inline-block;
          margin-right: 10px;
        }
      }
    }
    
    .main{
      width: 100%;
      position: absolute;
      top:60px;
      bottom: 0px;
      overflow: hidden;
      background-color: #D3DCE6;
      .el-aside {
      background-color: #D3DCE6;
      color: #333;
      text-align: center;
      line-height: 200px;
        .el-menu{
          height: 100%;
        }
    }
    
      .el-main {
        background-color: #fff;
        color: #333;
        text-align: center;
      }
    }
}
</style>



